
.left {
  float: left;
}
.right {
  float: right;
}
.login {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-size: 100% 100%;
}
.login .login_top {
  width: calc(100% - 10.133333rem);
  height: 1.013333rem;
  padding: 0.16rem 5.066667rem;
  background-color: rgba(255,255,255,0.3);
}
.login .login_top .top_logo {
  width: 1.173333rem;
  height: 1.013333rem;
}
.login .login_top .top_logo img {
  width: 100%;
}
.login .login_top .top_title {
  width: 2.666667rem;
  height: auto;
  margin-top: 0.053333rem;
  margin-left: 0.053333rem;
}
.login .login_top .top_title .title_top {
  color: #333;
  font-size: 0.373333rem;
  font-weight: 500;
  letter-spacing: 0.16rem;
}
.login .login_top .top_title .top_buttom {
  color: #666;
  letter-spacing: 0.16rem;
}
.login .login_top .top_btn {
  width: 1.733333rem;
  height: 0.666667rem;
  margin-top: 0.16rem;
  border-radius: 0.666667rem;
  background-color: #2597f2;
  text-align: center;
  line-height: 0.666667rem;
  font-size: 0.373333rem;
  color: #fff;
  font-weight: 400;
  cursor: pointer;
}
.login .login_center {
  width: 7.333333rem;
  height: auto;
  border: 0.026667rem solid #d0d0d0;
  border-radius: 0.266667rem;
  background-color: rgba(255,255,255,0.9);
  margin-top: 2.08rem;
  margin-left: 5.28rem;
}
.login .login_center .center_login {
  width: 4.533333rem;
  margin: 0.933333rem auto;
}
.login .login_center .center_login .center_title .title_top {
  color: #333;
  font-size: 0.533333rem;
  letter-spacing: 0.16rem;
}
.login .login_center .center_login .center_title .title_bottom {
  color: #666;
  letter-spacing: 0.16rem;
}
.login .login_center .center_login .center_input {
  margin-top: 0.64rem;
}
.login .login_center .center_login .center_input .input_item {
  width: 100%;
  height: 0.746667rem;
  border-bottom: 0.026667rem solid #b9b9b9;
  padding-top: 0.453333rem;
}
.login .login_center .center_login .center_input .input_item:last-child {
  padding-top: 0.24rem;
  color: #666;
  border: 0;
}
.login .login_center .center_login .center_input .input_item .item_icon {
  width: 0.4rem;
  height: 0.4rem;
  margin: 0.186667rem 0.133333rem;
  float: left;
}
.login .login_center .center_login .center_input .input_item .item_input {
  float: left;
  width: calc(100% - 0.4rem - 0.266667rem);
  height: 0.746667rem;
  padding: 0;
  margin: 0;
  border: 0;
  background: transparent;
  outline: none;
  font-size: 0.32rem;
  color: #666;
}
.login .login_center .center_login .center_btn {
  margin-top: 0.64rem;
  width: 100%;
  height: 0.8rem;
  line-height: 0.8rem;
  text-align: center;
  font-size: 0.373333rem;
  font-weight: 400;
  color: #fff;
  background-color: #2597f2;
  border-radius: 0.8rem;
  cursor: pointer;
}